@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
:root{
  --dark-bg-color: #282c34;
  --navbar-bg-color: #383c46;
  --blue-primary: #2196f3;
  --green-success: #69f0ae;
}
* {
  background-color: #282c34;
  background-color: var(--dark-bg-color);
}

/* Google fonts */
@font-face {
  font-family: 'Armata';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Armata Regular'), local('Armata-Regular'), url(https://fonts.gstatic.com/s/armata/v11/gokvH63_HV5jQ-E9lA.ttf) format('truetype');
}


/* Navbar styles */

/* Background color */
#navbar,
#text-brand,
.nav-links,
span,
.svg-nav,
#responsive-navbar-nav {
  background-color: var(--navbar-bg-color);
}

/* Font style */
#navbar {
  font-family: 'Armata', sans-serif;
  /* font-family: 'Major Mono Display', monospace; */
}
#text-brand{
  font-family: 'Major Mono Display', monospace;
}

/* Icon */
#icon {
  margin: 0 0.4em 0 0;
}

/* Links style */
span {
  color: var(--blue-primary);
  visibility: hidden;
}

.nav-links:hover span {
  visibility: visible;
}

#nav-brand:hover{
  color: var(--blue-primary);
}

.nav-links:active span, #nav-brand:active{
  color: var(--green-success);
}

/* Home styles */

#home-container{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#me{
  background-color: var(--dark-bg-color);
}

#home{
  text-align: center;
  color: white;
  font-family: 'Armata', sans-serif;
}

#home-intro{
  color: var(--blue-primary);
  font-family: 'Poiret One', cursive;
}
/* About educaion styles */

/* Background */
.list-item, .fa, .link-name{
  background-color: var(--navbar-bg-color);
}

/* Font */
.list-item{
  font-family: 'Armata', sans-serif;
  font-size: 0.9em;
  
}

.fa{
  font-size: large;
}

@media only screen and (max-width: 640px) {
  .list-item, .fa {
    font-size: smaller;
  }
}

/* Fucking alignment */
.link-btn{
  position: absolute;
  right: 1em;
  top: 0.5em;
  bottom: 0.5em;
  
}
.link-name{
  display: inline;
}

/* Media queries */
@media only screen and (max-width: 640px) {
  .link-btn{
    display: none;
  }
  
  .link-name{
    display: none;
  }

  .hidden-link{
    visibility: visible;
  }
}

@media only screen and (min-width: 641px) {

  .hidden-link{
    visibility: hidden;
    display: none;
  }
}

/* About technology styles */

/* Background */
.tech-bg, .bi{
  background-color: var(--navbar-bg-color);
}

/* Border */
.tech-border{
  border: none;
}

/* Font */
.tech-title {
  font-family: 'Armata', sans-serif;
  font-size: 0.9em;
}

/* Icons */
.tech-ico{
  background-color: var(--navbar-bg-color);
  margin-right: 0.5em;
  font-size: 2em;
}

/* Media query */
@media only screen and (max-width: 640px) {
  .tech-title {
    font-size: smaller;
  }
  .tech-ico{
    font-size: 1.2em;
  }
}



/* About skills styles */

/* Background */
.skills-item{
  background-color: var(--navbar-bg-color);
}

/* Font */
.skills-item{
  font-family: 'Armata', sans-serif;
  font-size: 0.9em;
}

@media only screen and (max-width: 640px) {
  .skills-item {
    font-size: smaller;
  }
}
/* About styles */

/* About width */
#about{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Container */
#about-container{
  width: 50vw;
}

@media only screen and (max-width: 812px) {
  #about-container {
    width: 90vw;
  }
}

/* Title */
#about-title{
  color: white;
  font-family: 'Armata', sans-serif;
  text-align: center;
}

/* About content */
#about-content{
  color: white;
}

.card-bg{
  background-color: var(--dark-bg-color);
}

/* Work styles */

/* Work width */
#work{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Container */
#work-container{
  width: 90vw;
}

/* Title */
#work-title{
  color: white;
  font-family: 'Armata', sans-serif;
  text-align: center;
}

/* Work content */
#work-content{
  color: white;
}


/* Card styles */

/* Card bg */
.card-header, .card-body, .card-text, .card-footer{
  background-color:var(--navbar-bg-color);
}

/* Button */
.card-btn{
  display: block;
  margin: auto;
  
  width: 5em;
}

.btn-ico{
  background-color: transparent;
  font-weight: bolder;
}

/* Font */
.card-header, .card-body, .card-text{
  font-family: 'Armata', sans-serif;
}

.card-header{
  font-size: small;
}

/* Card icon */
.card-icon{
  display: block;
  font-size: 3em;
  background-color: var(--navbar-bg-color);
  margin-bottom: 0.1em;
}

@media only screen and (max-width: 641px) {
   .card-icon {
    font-size: 3em;
  }
  .card-header{
    font-size: smaller;
  }
}

/* Contact styles */

/* Contact width */
#contact{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Container */
#contact-container{
  width: 100vw;
}

/* Contact title */
  #contact-title{
    color: white;
    font-family: 'Armata', sans-serif;
    text-align: center;
  }
/* contact subtitle */
#contact-subtitle{
  color: var(--blue-primary);
  font-family: 'Poiret One', cursive;
  text-align: center;
}

/* Email icon */
#contact-email{
    color: white;
    font-family: 'Armata', sans-serif;
    text-align: center;
    font-size: small;
}

/* Contact buttons */
#contact-buttons{
    text-align: center;
    margin-top: 1em;
}

.btn-circle{
  border-radius: 50%;
  text-align: center;
}

.contact-icon{
  background-color: transparent;
}
